.pagenotfound {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  padding: 80px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 4px 14px 0 rgb(0 0 0 / 20%);
}

.pagenotfound button {
  margin: 10px 0px;
  height: 40px;
  width: 50%;
  max-width: 150px;
  min-width: 150px;
  font-size: 23px;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 4px;
  background: #5c5ed6;
  border-bottom: 4px solid #1d098d;
  border-right: 4px solid #1d098d;
  color: white;
  letter-spacing: 2px;
}

.pagenotfound > h1 {
  font-size: 70px;
  letter-spacing: 5px;
}

.pagenotfound > p {
  font-size: 30px;
}
